<template>
  <div class="fe-all-selector-box" :style="{height: defaultRowHeight + 0 + 'px'}">

  </div>
</template>

<script>
export default {
  name: 'AllSelector',
  computed: {
    cols () {
      return this.$store.state.cols
    },
    colNum () {
      return this.$store.state.colNum
    },
    defaultRowHeight () {
      return this.$store.state.config.defaultRowHeight
    },
    defaultColWidth () {
      return this.$store.state.config.defaultColWidth
    }
  }
}
</script>

<style scoped>
.fe-all-selector-box {
  width: 50px;
  height: 20px;
  float: left;
  background: #a9c4e9;
  border: solid 1px #ddd;
  box-sizing: border-box;
}
</style>



