<template draggable="false" ondragstart="javascript: return false;">
  <div class="fe-toolbar-buttons">

    <el-dialog :visible.sync="borderPropertyDialog" width="550px" custom-class="">
      <div slot="title" class="fe-toolbar-buttons-border-property-dialog-title">
        边框属性
      </div>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend>线条</legend>
        <el-select v-model="borderStyle" placeholder="请选择" size="mini">
          <el-option
            v-for="item in borderPropertyStyleOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </fieldset>
      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend>预置</legend>
        <el-button type="primary" size="mini" @click="setBorderNone">
          <i class="iconfont fe-iconfont-border-none">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">无边框</span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderOuter">
          <i class="iconfont fe-iconfont-border-outer">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">外边框</span>
          </i>
        </el-button>
        <el-button type="primary" size="mini" @click="setBorderInner">
          <i class="iconfont fe-iconfont-border-inner">
            <span class="fe-toolbar-buttons-border-property-dialog-font-mini">内边框</span>
          </i>
        </el-button>
      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset" style="float: left; width: 200px;">
        <legend>颜色</legend>
        <el-color-picker v-model="borderColor" :predefine="predefinedColors" size="small"></el-color-picker>

        <!--<el-radio-group v-model="borderColor" size="small">-->
          <!--<el-radio-button label="#000000">黑色</el-radio-button>-->
          <!--<el-radio-button label="#FF0000">红色</el-radio-button>-->
        <!--</el-radio-group>-->
      </fieldset>

      <fieldset class="fe-toolbar-buttons-border-property-dialog-fieldset">
        <legend>自定义</legend>
        <table class="fe-toolbar-buttons-border-property-dialog-table-border-buttons">
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderTop">
                <i class="iconfont fe-iconfont-border-top"></i>
              </el-button>
            </td>
            <td colspan="3" rowspan="3">
              <!--<table cellspacing="0" cellpadding="0" class="fe-toolbar-preview fe-toolbar-preview-top fe-toolbar-preview-horizon fe-toolbar-preview-bottom fe-toolbar-preview-left fe-toolbar-preview-vertical fe-toolbar-preview-right"-->
                <!--v-bind:class="{-->
                    <!--feToolbarPreviewTop: borderPosition.top-->
                <!--}"-->
              <!--&gt;-->
              <table cellspacing="0" cellpadding="0" class="fe-toolbar-preview"
                     v-bind:class="{
                      // 'fe-toolbar-preview-top-width': borderTop.width,
                      // 'fe-toolbar-preview-horizon-width': borderHorizon.width,
                      // 'fe-toolbar-preview-bottom-width': borderBottom.width,
                      // 'fe-toolbar-preview-left-width': borderLeft.width,
                      // 'fe-toolbar-preview-vertical-width': borderVertical.width,
                      // 'fe-toolbar-preview-right-width': borderRight.width,
                      }"
                     :style="{
                      borderTopColor: borderTop.color,
                      borderBottomColor: borderBottom.color,
                      borderLeftColor: borderLeft.color,
                      borderRightColor: borderRight.color,
                      borderTopStyle: borderTop.style,
                      borderBottomStyle: borderBottom.style,
                      borderLeftStyle: borderLeft.style,
                      borderRightStyle: borderRight.style,
                      borderTopWidth: borderTop.width,
                      borderBottomWidth: borderBottom.width,
                      borderLeftWidth: borderLeft.width,
                      borderRightWidth: borderRight.width,
                     }"
              >
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderBottomColor: borderHorizon.color,
                      borderRightStyle: borderVertical.style,
                      borderBottomStyle: borderHorizon.style,
                      borderRightWidth: borderVertical.width,
                      borderBottomWidth: borderHorizon.width,
                     }"
                  ></td>
                  <td :style="{
                      borderBottomColor: borderHorizon.color,
                      borderBottomStyle: borderHorizon.style,
                      borderBottomWidth: borderHorizon.width
                     }"
                  ></td>
                </tr>
                <tr>
                  <td :style="{
                      borderRightColor: borderVertical.color,
                      borderRightStyle: borderVertical.style,
                      borderRightWidth: borderVertical.width
                     }"
                  ></td>
                  <td></td>
                </tr>
              </table>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderHorizon">
                <i class="iconfont fe-iconfont-border-horizontal"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td>
              <el-button type="primary" size="mini" @click="setBorderBottom">
                <i class="iconfont fe-iconfont-borderbottom"></i>
              </el-button>
            </td>
          </tr>
          <tr>
            <td></td>
            <td style="width: 50px; text-align: left">
              <el-button type="primary" size="mini" @click="setBorderLeft">
                <i class="iconfont fe-iconfont-borderleft"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: center">
              <el-button type="primary" size="mini" @click="setBorderVertical">
                <i class="iconfont fe-iconfont-border-vertical"></i>
              </el-button>
            </td>
            <td style="width: 50px; text-align: right">
              <el-button type="primary" size="mini" @click="setBorderRight">
                <i class="iconfont fe-iconfont-border-right"></i>
              </el-button>
            </td>
          </tr>
        </table>
        <br/>
      </fieldset>
      <div slot="footer" class="dialog-footer">
        <el-button size="mini" @click="borderPropertyDialog = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="setCellsBorder({borderTop, borderHorizon, borderBottom, borderLeft, borderVertical, borderRight}); borderPropertyDialog = false;">确 定</el-button>
      </div>
    </el-dialog>

    <div class="fe-toolbar-block">
      <!--撤销、重做-->
      <el-button-group>
        <el-button type="primary" size="mini" @click="undo" :disabled="!isUndoEnable" title="撤销" >
          <i class="iconfont fe-iconfont-undo"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="redo" :disabled="!isRedoEnable" title="重做">
          <i class="iconfont fe-iconfont-redo"></i>
        </el-button>
      </el-button-group>
      <!--合并、分割-->
      <el-button-group>
        <el-button type="primary" size="mini" @click="mergeCells" title="合并单元格">
          <i class="iconfont fe-iconfont-mergecell"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="splitCells" title="取消合并">
          <i class="iconfont fe-iconfont-separatecell"></i>
        </el-button>
      </el-button-group>

      <el-button-group>
        <el-button type="primary" size="mini" @click="showBorderPropertyDialog">
          <i class="iconfont fe-iconfont-border-all"></i>
        </el-button>
      </el-button-group>

      <el-button-group>
        <el-button type="primary" size="mini" @click="beginUEdit" title="编辑">
          <i class="iconfont fe-iconfont-edit"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="clearCellsContent" title="清空内容">
          <i class="iconfont fe-iconfont-clear"></i>
        </el-button>
      </el-button-group>

      <!--复制、剪切、粘贴-->
      <el-button-group>
        <el-button type="primary" size="mini" @click="copyCell" title="复制" >
          <i class="iconfont fe-iconfont-copy"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="redo" title="剪切">
          <i class="iconfont fe-iconfont-cut"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="redo" title="粘贴">
          <i class="iconfont fe-iconfont-paste"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="redo" title="取消复制、粘贴">
          <i class="iconfont fe-iconfont-cancel"></i>
        </el-button>
      </el-button-group>
    </div>

    <div class="fe-toolbar-block">
      <!--粗体、斜体、下划线-->
      <el-button-group>
        <el-button type="primary" size="mini" @click="setBold" title="粗体">
          <i class="iconfont fe-iconfont-bold"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="setItalic" title="斜体">
          <i class="iconfont fe-iconfont-italic"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="setTextDecoration" title="下划线">
          <i class="iconfont fe-iconfont-underline"></i>
        </el-button>

      </el-button-group>

      <el-button-group>
        <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'left'})" title="居左">
          <i class="iconfont fe-iconfont-alignleft"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'center'})" title="居中">
          <i class="iconfont fe-iconfont-aligncenter"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="setCellsTextAlign({align: 'right'})" title="居右">
          <i class="iconfont fe-iconfont-alignright"></i>
        </el-button>
      </el-button-group>

      <el-select size="mini" :value="fontfamily" @change="setCellsTextFontFamily" placeholder="字体" >
        <el-option
          v-for="item in fontFamilyItems"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <el-select size="mini" :value="fontsize" @change="setCellsTextFontSize" placeholder="字号" >
        <el-option
          v-for="item in fontSizeItems"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>

      <el-button-group>
        <swatches :value="backgroundColor" colors="text-advanced" @input="setCellsBackground">
          <el-button slot="trigger" type="primary" size="mini" title="背景色">
            <i class="iconfont fe-iconfont-background-color">
            </i>
          </el-button>
        </swatches>
        <swatches :value="textColor" colors="text-advanced" @input="setCellsTextColor">
          <el-button slot="trigger" type="primary" size="mini" title="背景色">
            <i class="iconfont fe-iconfont-text-color">
            </i>
          </el-button>
        </swatches>
      </el-button-group>
    </div>

    <div class="fe-toolbar-block">
      <el-button-group>
        <el-button type="primary" size="mini" @click="addControlConfirm('input')" title="文本框">
          <i class="iconfont fe-iconfont-input"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="addControlConfirm('textarea')" title="文本域/多行文本">
          <i class="iconfont fe-iconfont-text"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="addControlConfirm('date')" title="日期控件">
          <i class="iconfont fe-iconfont-date"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="addControlConfirm('select')" title="下拉框">
          <i class="iconfont fe-iconfont-drop-down"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="addControlConfirm('checkbox')" title="多选框">
          <i class="iconfont fe-iconfont-checkbox"></i>
        </el-button>
        <el-button type="primary" size="mini" @click="addControlConfirm('radio')" title="单选框">
          <i class="iconfont fe-iconfont-radio"></i>
        </el-button>
      </el-button-group>
    </div>

    <!--input-->
    <el-dialog v-if="editControl.type === 'input'" :visible.sync="editControl.type === 'input'" :width="controlDialog.dialogWidth" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        文本框属性
      </div>
      <el-form :model="controlDialog.inputAttribute" ref="inputForm" :rules="controlDialog.inputRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model="controlDialog.inputAttribute.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.number="controlDialog.inputAttribute.width" :min="0" :max="999" placeholder="0表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addInputSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--textarea-->
    <el-dialog v-else-if="editControl.type === 'textarea'" :visible.sync="editControl.type === 'textarea'" :width="controlDialog.dialogWidth" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        文本域属性
      </div>
      <el-form :model="controlDialog.textareaAttribute" ref="textareaForm" :rules="controlDialog.textareaRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model="controlDialog.textareaAttribute.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.number="controlDialog.textareaAttribute.width" :min="0" :max="999" placeholder="0表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="height">
          <el-input v-model.number="controlDialog.textareaAttribute.height" :min="0" :max="999" placeholder="0表示和单元格等高" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addTextareaSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--date-->
    <el-dialog v-else-if="editControl.type === 'date'" :visible.sync="editControl.type === 'date'" :width="controlDialog.dialogWidth" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        日期控件属性
      </div>
      <el-form :model="controlDialog.dateAttribute" ref="dateForm" :rules="controlDialog.dateRules" label-width="80px" size="mini">
        <el-form-item label="控件名称" prop="name">
          <el-input v-model="controlDialog.dateAttribute.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="控件宽度" prop="width">
          <el-input v-model.number="controlDialog.dateAttribute.width" :min="0" :max="999" placeholder="0表示和单元格等宽" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="日期格式" prop="height">
          <!--<el-input v-model.number="controlDialog.dateAttribute.height" placeholder="0表示和单元格等高" autocomplete="off"></el-input>-->

          <el-select v-model="controlDialog.dateAttribute.format" placeholder="请选择日期格式" style="width: 100% !important;">
            <el-option key="yyyy-MM-dd" label="yyyy-MM-dd" value="yyyy-MM-dd"/>
            <el-option key="yyyy年MM月dd日" label="yyyy年MM月dd日" value="yyyy年MM月dd日"/>
          </el-select>

        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addDateSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--select-->
    <el-dialog v-else-if="editControl.type === 'select'" :visible.sync="editControl.type === 'select'" :width="controlDialog.dialogWidth2" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        下拉框属性
      </div>
      <el-form :model="controlDialog.selectAttribute" ref="selectForm" :rules="controlDialog.selectRules" label-width="80px" size="mini">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <el-form-item label="控件名称" prop="name">
              <el-input v-model="controlDialog.selectAttribute.name" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="控件宽度" prop="width">
              <el-input v-model.number="controlDialog.selectAttribute.width" :min="0" :max="999" placeholder="0表示和单元格等宽" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="controlDialog.selectAttribute.options"
              height="280"
              border
              style="width: 100%">
              <el-table-column
                prop="value"
                label="值"
                width="150">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.value" placeholder="请输入值"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                prop="label"
                label="显示文本"
                width="180">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.text" placeholder="请输入显示文本"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="操作"
                width="120">
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button type="primary" size="mini" icon="el-icon-delete" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-up" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-down" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                  </el-button-group>
                  <!--<el-button type="text" size="mini" @click="controlDialog.selectAttribute.options.splice(scope.$index, 1)">移除</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== 0" @click="controlDialog.selectAttribute.options.splice(scope.$index - 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])">上移</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== controlDialog.selectAttribute.options.length - 1" @click="controlDialog.selectAttribute.options.splice(scope.$index + 1, 0, controlDialog.selectAttribute.options.splice(scope.$index, 1)[0])">下移</el-button>-->
                </template>
              </el-table-column>
              <div slot="empty">
                <div>
                  暂无数据...
                </div>
                <div>
                  <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.selectAttribute.options.push({})"></el-button>
                </div>
              </div>
              <div slot="append" style="text-align: center; padding: 5px;">
                <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.selectAttribute.options.push({})"></el-button>
              </div>
            </el-table>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addSelectSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--checkbox-->
    <el-dialog v-else-if="editControl.type === 'checkbox'" :visible.sync="editControl.type === 'checkbox'" :width="controlDialog.dialogWidth2" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        多选框属性
      </div>
      <el-form :model="controlDialog.checkboxAttribute" ref="checkboxForm" :rules="controlDialog.checkboxRules" label-width="80px" size="mini">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <el-form-item label="控件名称" prop="name">
              <el-input v-model="controlDialog.checkboxAttribute.name" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2"></el-col>
          <el-col :span="10">
            <el-switch
              v-model="controlDialog.checkboxAttribute.horizon"
              active-text="水平布局"
              inactive-text="垂直布局">
            </el-switch>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="controlDialog.checkboxAttribute.options"
              height="280"
              border
              style="width: 100%">
              <el-table-column
                prop="value"
                label="值"
                width="150">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.value" placeholder="请输入值"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                prop="label"
                label="显示文本"
                width="180">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.text" placeholder="请输入显示文本"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="操作"
                width="120">
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button type="primary" size="mini" icon="el-icon-delete" @click="controlDialog.checkboxAttribute.options.splice(scope.$index, 1)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-up" v-if="scope.$index !== 0" @click="controlDialog.checkboxAttribute.options.splice(scope.$index - 1, 0, controlDialog.checkboxAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-down" v-if="scope.$index !== controlDialog.checkboxAttribute.options.length - 1" @click="controlDialog.checkboxAttribute.options.splice(scope.$index + 1, 0, controlDialog.checkboxAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                  </el-button-group>
                  <!--<el-button type="text" size="mini" @click="controlDialog.checkboxAttribute.options.splice(scope.$index, 1)">移除</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== 0" @click="controlDialog.checkboxAttribute.options.splice(scope.$index - 1, 0, controlDialog.checkboxAttribute.options.splice(scope.$index, 1)[0])">上移</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== controlDialog.checkboxAttribute.options.length - 1" @click="controlDialog.checkboxAttribute.options.splice(scope.$index + 1, 0, controlDialog.checkboxAttribute.options.splice(scope.$index, 1)[0])">下移</el-button>-->
                </template>
              </el-table-column>
              <div slot="empty">
                <div>
                  暂无数据...
                </div>
                <div>
                  <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.checkboxAttribute.options.push({})"></el-button>
                </div>
              </div>
              <div slot="append" style="text-align: center; padding: 5px;">
                <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.checkboxAttribute.options.push({})"></el-button>
              </div>
            </el-table>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addCheckboxSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--radio-->
    <el-dialog v-else-if="editControl.type === 'radio'" :visible.sync="editControl.type === 'radio'" :width="controlDialog.dialogWidth2" @opened="setDialogForm" @close="closeDialog">
      <div slot="title" class="fe-dialog-title">
        多选框属性
      </div>
      <el-form :model="controlDialog.radioAttribute" ref="radioForm" :rules="controlDialog.radioRules" label-width="80px" size="mini">
        <el-row type="flex" class="row-bg">
          <el-col :span="12">
            <el-form-item label="控件名称" prop="name">
              <el-input v-model="controlDialog.radioAttribute.name" autocomplete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2"></el-col>
          <el-col :span="10">
            <el-switch
              v-model="controlDialog.radioAttribute.horizon"
              active-text="水平布局"
              inactive-text="垂直布局">
            </el-switch>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-table
              :data="controlDialog.radioAttribute.options"
              height="280"
              border
              style="width: 100%">
              <el-table-column
                prop="value"
                label="值"
                width="150">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.value" placeholder="请输入值"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                prop="label"
                label="显示文本"
                width="180">
                <template slot-scope="scope">
                  <el-input v-model="scope.row.text" placeholder="请输入显示文本"></el-input>
                </template>
              </el-table-column>
              <el-table-column
                label="操作"
                width="120">
                <template slot-scope="scope">
                  <el-button-group>
                    <el-button type="primary" size="mini" icon="el-icon-delete" @click="controlDialog.radioAttribute.options.splice(scope.$index, 1)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-up" v-if="scope.$index !== 0" @click="controlDialog.radioAttribute.options.splice(scope.$index - 1, 0, controlDialog.radioAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-arrow-down" v-if="scope.$index !== controlDialog.radioAttribute.options.length - 1" @click="controlDialog.radioAttribute.options.splice(scope.$index + 1, 0, controlDialog.radioAttribute.options.splice(scope.$index, 1)[0])"></el-button>
                  </el-button-group>
                  <!--<el-button type="text" size="mini" @click="controlDialog.radioAttribute.options.splice(scope.$index, 1)">移除</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== 0" @click="controlDialog.radioAttribute.options.splice(scope.$index - 1, 0, controlDialog.radioAttribute.options.splice(scope.$index, 1)[0])">上移</el-button>-->
                  <!--<el-button type="text" size="mini" v-if="scope.$index !== controlDialog.radioAttribute.options.length - 1" @click="controlDialog.radioAttribute.options.splice(scope.$index + 1, 0, controlDialog.radioAttribute.options.splice(scope.$index, 1)[0])">下移</el-button>-->
                </template>
              </el-table-column>
              <div slot="empty">
                <div>
                  暂无数据...
                </div>
                <div>
                  <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.radioAttribute.options.push({})"></el-button>
                </div>
              </div>
              <div slot="append" style="text-align: center; padding: 5px;">
                <el-button type="primary" size="mini" icon="el-icon-plus" @click="controlDialog.radioAttribute.options.push({})"></el-button>
              </div>
            </el-table>
          </el-col>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeDialog" size="mini">取 消</el-button>
        <el-button type="primary" @click="addRadioSave" size="mini">确 定</el-button>
      </div>
    </el-dialog>

    <!--<el-button-group>-->
      <!--&lt;!&ndash;<el-button type="primary" size="mini" title="字体颜色">&ndash;&gt;-->
        <!--&lt;!&ndash;<i class="iconfont fe-iconfont-text-color" @click="showPicker1">&ndash;&gt;-->
          <!--&lt;!&ndash;<el-color-picker ref="p1" id="picker1" size="mini" style="width: 1px; height: 1px; display: none" popper-class="fe-toolbar-buttons-color-picker1" @change="setCellsBackground($event)"></el-color-picker>&ndash;&gt;-->
        <!--&lt;!&ndash;</i>&ndash;&gt;-->
      <!--&lt;!&ndash;</el-button>&ndash;&gt;-->
      <!--<el-button type="primary" size="mini" @click="showPicker3" title="背景色">-->
        <!--<i class="iconfont fe-iconfont-background-color">-->
          <!--<el-color-picker ref="p2" id="picker2" size="mini" style="width: 1px; height: 1px; display: none" popper-class="fe-toolbar-buttons-color-picker2" @change="setCellsBackground($event)"></el-color-picker>-->
        <!--</i>-->
      <!--</el-button>-->
    <!--</el-button-group>-->

  </div>
</template>

<script>
import { mapActions } from 'vuex'
import { Message, MessageBox } from 'element-ui'
import { getLocalStorageItem, setLocalStorageItem } from '../util'
import { CONST_LOCALSTORATE_STATELIST_KEY, CONST_LOCALSTORATE_STATEINDEX_KEY } from '../const'
import Swatches from 'vue-swatches'

// Import the styles too, globally
import 'vue-swatches/dist/vue-swatches.min.css'
export default {
  name: 'Toolbar',
  components: {
    Swatches
  },
  computed: {
    type () {
      return this.$store.state.cache.type
    },
    editControl () {
      return this.$store.state.cache.editControl
    },
    fontFamilyItems () {
      return this.$store.state.toolbarConfig.fontFamilyItems
    },
    fontSizeItems () {
      return this.$store.state.toolbarConfig.fontSizeItems
    },
    predefinedColors () {
      return this.$store.state.config.predefinedColors
    },
    backList () {
      return this.$store.state.backList
    },
    nextList () {
      return this.$store.state.nextList
    },
    isUndoEnable () {
      // let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      let stateIndex = this.$store.state.stateIndex
      // console.log('---', getLocalStorageItem(CONST_LOCALSTORATE_STATEINDEX_KEY))
      return stateIndex > 0
      //
      // if (stateIndex > 0) {
      //   return true
      // }
      //
      // return true
    },
    isRedoEnable () {
      let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      let stateIndex = this.$store.state.stateIndex
      console.log('---', stateIndex, stateList)
      return stateIndex < stateList.length - 1 && stateIndex >= 0
    },
    fontsize () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].fontSize
      }

      return ''
    },
    fontfamily () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].fontFamily
      }

      return ''
    },
    backgroundColor () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].background
      }

      return ''
    },
    textColor () {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col

      if (beginRow && beginCol && this.$store.state.cells[beginRow][beginCol]) {
        return this.$store.state.cells[beginRow][beginCol].color
      }

      return ''
    }
  },
  data: function () {
    return {
      tempColor: '',
      borderPropertyDialog: false,
      borderPropertyStyleOptions: [
        {value: 'none', label: '无'},
        {value: 'solid', label: '实线'},
        {value: 'dotted', label: '短虚线'},
        {value: 'dashed', label: '长虚线'}
      ],
      borderStyle: '',
      borderColor: '',
      borderWidth: '1',
      borderTop: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderHorizon: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderBottom: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderLeft: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderVertical: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderRight: {
        style: undefined,
        width: undefined,
        color: undefined
      },
      borderPosition: {
        top: undefined,
        horizon: undefined,
        bottom: undefined,
        left: undefined,
        vertical: undefined,
        right: undefined
      },
      controlDialog: {
        dialogWidth: '300px',
        dialogWidth2: '500px',

        inputDialog: false,
        inputAttribute: {
          name: '',
          width: ''
        },
        inputRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ]
        },

        textareaDialog: false,
        textareaAttribute: {
          name: '',
          width: '',
          height: ''
        },
        textareaRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ],
          height: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ]
        },

        dateDialog: false,
        dateAttribute: {
          name: '',
          width: ''
        },
        dateRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ],
          format: [
            { required: true, message: '请选择格式', trigger: 'blur' }
          ]
        },

        selectDialog: false,
        selectAttribute: {
          name: '',
          width: '',
          options: []
        },
        selectRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ],
          format: [
            { required: true, message: '请选择格式', trigger: 'blur' }
          ]
        },

        checkboxDialog: false,
        checkboxAttribute: {
          name: '',
          horizon: true,
          options: []
        },
        checkboxRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ],
          format: [
            { required: true, message: '请选择格式', trigger: 'blur' }
          ]
        },

        radioDialog: false,
        radioAttribute: {
          name: '',
          horizon: true,
          options: []
        },
        radioRules: {
          name: [
            { required: true, message: '请输入控件名称', trigger: 'blur' }
          ],
          width: [
            { type: 'number', min: 0, max: 999, message: '请输入0-999之间的数字' }
          ],
          format: [
            { required: true, message: '请选择格式', trigger: 'blur' }
          ]
        }
      }
    }
  },
  methods: {
    setBorderNone () {
      this.borderPosition.top = false
      this.borderPosition.bottom = false
      this.borderPosition.left = false
      this.borderPosition.right = false
      this.borderPosition.horizon = false
      this.borderPosition.vertical = false

      this.borderTop.style = ''
      this.borderTop.width = ''
      this.borderTop.color = ''
      this.borderHorizon.style = ''
      this.borderHorizon.width = ''
      this.borderHorizon.color = ''
      this.borderBottom.style = ''
      this.borderBottom.width = ''
      this.borderBottom.color = ''
      this.borderLeft.style = ''
      this.borderLeft.width = ''
      this.borderLeft.color = ''
      this.borderVertical.style = ''
      this.borderVertical.width = ''
      this.borderVertical.color = ''
      this.borderRight.style = ''
      this.borderRight.width = ''
      this.borderRight.color = ''
    },
    setBorderOuter () {
      this.borderPosition.top = true
      this.borderPosition.bottom = true
      this.borderPosition.left = true
      this.borderPosition.right = true

      this.borderTop.style = this.borderStyle
      this.borderTop.width = '1'
      this.borderTop.color = this.borderColor
      this.borderBottom.style = this.borderStyle
      this.borderBottom.width = '1'
      this.borderBottom.color = this.borderColor
      this.borderLeft.style = this.borderStyle
      this.borderLeft.width = '1'
      this.borderLeft.color = this.borderColor
      this.borderRight.style = this.borderStyle
      this.borderRight.width = '1'
      this.borderRight.color = this.borderColor
    },
    setBorderInner () {
      this.borderPosition.horizon = true
      this.borderPosition.vertical = true

      this.borderHorizon.style = this.borderStyle
      this.borderHorizon.width = '1'
      this.borderHorizon.color = this.borderColor
      this.borderVertical.style = this.borderStyle
      this.borderVertical.width = '1'
      this.borderVertical.color = this.borderColor
    },
    setBorderLeft () {
      if (this.borderPosition.left) {
        this.borderLeft.color = ''
        this.borderLeft.style = ''
        this.borderLeft.width = ''
        this.borderPosition.left = false
      } else {
        this.borderLeft.color = this.borderColor
        this.borderLeft.style = this.borderStyle
        this.borderLeft.width = '1'
        this.borderPosition.left = true
      }
    },
    setBorderVertical () {
      if (this.borderPosition.vertical) {
        this.borderVertical.color = ''
        this.borderVertical.style = ''
        this.borderVertical.width = ''
        this.borderPosition.vertical = false
      } else {
        this.borderVertical.color = this.borderColor
        this.borderVertical.style = this.borderStyle
        this.borderVertical.width = '1'
        this.borderPosition.vertical = true
      }
    },
    setBorderRight () {
      if (this.borderPosition.right) {
        this.borderRight.color = ''
        this.borderRight.style = ''
        this.borderRight.width = ''
        this.borderPosition.right = false
      } else {
        this.borderRight.color = this.borderColor
        this.borderRight.style = this.borderStyle
        this.borderRight.width = '1'
        this.borderPosition.right = true
      }
    },
    setBorderTop () {
      if (this.borderPosition.top) {
        this.borderTop.color = ''
        this.borderTop.style = ''
        this.borderTop.width = ''
        this.borderPosition.top = false
      } else {
        this.borderTop.color = this.borderColor
        this.borderTop.style = this.borderStyle
        this.borderTop.width = '1'
        this.borderPosition.top = true
      }
    },
    setBorderHorizon () {
      if (this.borderPosition.horizon) {
        this.borderHorizon.color = ''
        this.borderHorizon.style = ''
        this.borderHorizon.width = ''
        this.borderPosition.horizon = false
      } else {
        this.borderHorizon.color = this.borderColor
        this.borderHorizon.style = this.borderStyle
        this.borderHorizon.width = '1'
        this.borderPosition.horizon = true
      }
    },
    setBorderBottom () {
      if (this.borderPosition.bottom) {
        this.borderBottom.color = ''
        this.borderBottom.style = ''
        this.borderBottom.width = ''
        this.borderPosition.bottom = false
      } else {
        this.borderBottom.color = this.borderColor
        this.borderBottom.style = this.borderStyle
        this.borderBottom.width = '1'
        this.borderPosition.bottom = true
      }
    },
    setBorder () {

    },
    showBorderPropertyDialog () {
      this.borderStyle = 'solid'
      this.borderColor = '#000000'
      this.borderPropertyDialog = true
    },
    clearCellsContent () {
      MessageBox.confirm('是否要清除选中的内容', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.doClearCellsContent()

        Message.success({
          type: 'success',
          message: '清除成功'
        })
      }).catch(() => {
        Message.info({
          type: 'info',
          message: '已取消'
        })
      })
    },
    undo () {
      let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      if (stateList <= 1) {
        return
      }

      this.$emit('loading')
      let stateIndex = this.$store.state.stateIndex
      stateIndex = stateIndex - 1
      let backState = stateList[stateIndex]
      backState.stateIndex = stateIndex
      this.$store.replaceState(Object.assign({}, this.$store.state, backState))
      setLocalStorageItem(CONST_LOCALSTORATE_STATEINDEX_KEY, stateIndex)
      this.$emit('loading')
    },
    redo () {
      let stateList = JSON.parse(getLocalStorageItem(CONST_LOCALSTORATE_STATELIST_KEY) || '[]')
      let stateIndex = this.$store.state.stateIndex
      if (stateIndex >= stateList.length - 1) {
        return
      }

      this.$emit('loading')
      stateIndex = stateIndex + 1
      let nextState = stateList[stateIndex]
      nextState.stateIndex = stateIndex
      this.$store.replaceState(Object.assign({}, this.$store.state, nextState))
      setLocalStorageItem(CONST_LOCALSTORATE_STATEINDEX_KEY, stateIndex)
      this.$emit('loading')
    },
    addInputSave () {
      this.$refs['inputForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.inputAttribute
          let type = 'input'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addTextareaSave () {
      this.$refs['textareaForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.textareaAttribute
          let type = 'textarea'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addDateSave () {
      this.$refs['dateForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.dateAttribute
          let type = 'date'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addSelectSave () {
      this.$refs['selectForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.selectAttribute
          let type = 'select'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addCheckboxSave () {
      this.$refs['checkboxForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.checkboxAttribute
          let type = 'checkbox'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addRadioSave () {
      this.$refs['radioForm'].validate((valid) => {
        if (valid) {
          let attribute = this.controlDialog.radioAttribute
          let type = 'radio'
          this.addControl({type, attribute})
          this.cancelAddControl()
        }
      })
    },
    addControlConfirm (type) {
      let beginRow = this.$store.state.cache.select.beginCell.row
      let beginCol = this.$store.state.cache.select.beginCell.col
      let endRow = this.$store.state.cache.select.endCell.row
      let endCol = this.$store.state.cache.select.endCell.col

      if (beginRow !== endRow || beginCol !== endCol) {
        this.beginSelectCell({row: beginRow, col: beginCol})
        this.endSelectCell({row: beginRow, col: beginCol})
      }

      let row = this.$store.state.cache.select.beginCell.row
      let col = this.$store.state.cache.select.beginCell.col

      let cells = this.$store.state.cells
      if (cells[row][col] && cells[row][col].content && cells[row][col].content !== '<br>') {
        console.log('content not empty')

        this.$confirm('单元格已存在内容，是否进行覆盖？', '提醒', {
          confirmButtonText: '确定',
          cancelButtonText: '取消'
        }).then(() => {
          this.beginAddControl({type})
        }).catch(() => {
        })
      } else {
        this.beginAddControl({type})
      }
    },
    closeDialog () {
      this.cancelAddControl()
    },
    setDialogForm () {
      console.log('setDialogForm')
      if (this.editControl.type === 'input') {
        this.controlDialog.inputAttribute.name = this.editControl.attribute.name
        this.controlDialog.inputAttribute.width = this.editControl.attribute.width
      } else if (this.editControl.type === 'textarea') {
        this.controlDialog.textareaAttribute.name = this.editControl.attribute.name
        this.controlDialog.textareaAttribute.width = this.editControl.attribute.width
        this.controlDialog.textareaAttribute.height = this.editControl.attribute.height
      } else if (this.editControl.type === 'date') {
        this.controlDialog.dateAttribute.name = this.editControl.attribute.name
        this.controlDialog.dateAttribute.width = this.editControl.attribute.width
        this.controlDialog.dateAttribute.format = this.editControl.attribute.format
      } else if (this.editControl.type === 'select') {
        this.controlDialog.selectAttribute.name = this.editControl.attribute.name
        this.controlDialog.selectAttribute.width = this.editControl.attribute.width
        this.controlDialog.selectAttribute.options = this.editControl.attribute.options
      } else if (this.editControl.type === 'checkbox') {
        this.controlDialog.checkboxAttribute.name = this.editControl.attribute.name
        this.controlDialog.checkboxAttribute.width = this.editControl.attribute.width
        this.controlDialog.checkboxAttribute.options = this.editControl.attribute.options
      } else if (this.editControl.type === 'radio') {
        this.controlDialog.radioAttribute.name = this.editControl.attribute.name
        this.controlDialog.radioAttribute.width = this.editControl.attribute.width
        this.controlDialog.radioAttribute.options = this.editControl.attribute.options
      }
    },
    ...mapActions([
      'beginSelectCell',
      'endSelectCell',
      'mergeSelectedCells',
      'mergeCells',
      'splitCells',
      'setCellsTextAlign',
      'setCellsBackground',
      'setCellsTextColor',
      'setCellsBorder',
      'beginUEdit',
      'doClearCellsContent',
      'doUndo',
      'doRedo',
      'copyCell',
      'setBold',
      'setTextDecoration',
      'setItalic',
      'setCellsTextFontSize',
      'setCellsTextFontFamily',
      'addControl',
      'beginAddControl',
      'cancelAddControl'
    ])
  },
  mounted: function () {
    console.log('toolbar')
  }
}
</script>

