  .fe-toolbar-buttons-border-property-dialog-title {
    font-size: 14px;
  }
  .fe-toolbar-block {
    float: left;
    padding: 0px 2px 4px 2px;
    height: 30px;
    line-height: 30px;
  }
  .vue-swatches {
    position: relative;
    outline: none;
    float: left;
  }
  .fe-dialog-title {
    font-size: 16px;
  }
</style>



