<template>

  <div
      v-if="contextMenu.menuItemList.length > 0"
      class="fe-content-menu"
      v-on:mouseout="hideContentMenu($event)"
      v-bind:style="{left: contextMenu.e.clientX + contextMenu.width / 2 - 2 + 'px', top: contextMenu.e.clientY - 2 + 'px', minWidth: contextMenu.width + 'px'}"
  >
    <ul>
      <li v-for="(item, index) in contextMenu.menuItemList" v-bind:key="index" v-bind:class="item.disabled ? 'fe-content-menu-disabled' : ''" v-on:click="item.handler">
        <i v-bind:class="'iconfont fe-content-menu-icon ' + item.icon"> </i> &nbsp; <span class="fe-content-menu-text">{{item.text }}</span>
      </li>
    </ul>
  </div>

</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'fe-content-menu',
  computed: {
    contextMenu () {
      return this.$store.state.contextMenu
    }
  },
  methods: {
    ...mapActions([
      'hideContentMenu'
    ])
  }
}
</script>

<style scoped>

  * {
    font-size: 14px;
  }

  .fe-content-menu {

    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    border-width: 1px;
    border-style: solid;
    border-color: #b0b3b8;
    position: fixed;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-transition: opacity .3s,-webkit-transform .4s;
    transition: opacity .3s,-webkit-transform .4s;
    transition: opacity .3s,transform .4s;
    transition: opacity .3s,transform .4s,-webkit-transform .4s;
    /*padding: 5px 5px 5px 10px;*/
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background: #fff;
    z-index: 99999999;
  }

  .fe-content-menu ul {
    padding: 0px;
    width: 100%;
    margin: 1px;
  }

  .fe-content-menu li {
    list-style: none;
    line-height: 24px;
    cursor: pointer;
    padding: 2px;
  }

  .fe-content-menu li:hover {
    background: #f3f3f3;
  }

  .fe-content-menu-disabled {
    color: #aaa;
  }

  .fe-content-menu-icon {
    /*font-size: 18px;*/
    line-height: 28px;
  }
  .fe-content-menu-text {
    font-size: 12px;
  }

</style>



