let contains = document.documentElement.contains
  ? function (parent, node) {
    return parent !== node && parent.contains(node)
  }
  : function (parent, node) {
    while (node && (node = node.parentNode)) { if (node === parent) return true }
    return false
  }

let isEventParentHasAttribute = function (e, attributeName) {
  for (let i in e.path) {
    if (e.path[i] === document.querySelector('body')) {
      return false
    }
    if (e.path[i].hasAttribute(attributeName)) {
      return e.path[i]
    }
  }
  return false
}

let toNumber = function (val) {
  return val - 0
}

let getLocalStorageItem = function (key) {
  return localStorage.getItem(key)
}

let setLocalStorageItem = function (key, value) {
  return localStorage.setItem(key, value)
}

export {contains, isEventParentHasAttribute, toNumber, getLocalStorageItem, setLocalStorageItem}



