<template>
  <div class="fe-ueditor-toolbar-buttons">
    <el-button-group>
      <el-button size="mini" @click="cancelUEdit" title="返回">
        <i class="iconfont fe-iconfont-back"></i>
      </el-button>
      <el-button type="primary" size="mini" @click="endEdit" title="确定">
        <i class="iconfont fe-iconfont-yes"></i>
      </el-button>
    </el-button-group>
    <el-button-group>
      <el-button size="mini" type="primary" title="普通文本框">
        <i class="iconfont ">文本框</i>
      </el-button>
      <el-button size="mini" type="primary" title="日期选择框">
        <i class="iconfont ">日期控件</i>
      </el-button>
      <el-button size="mini" type="primary" title="多行文本框">
        <i class="iconfont ">多行文本</i>
      </el-button>
    </el-button-group>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'NEditorToolbar',
  methods: {
    endEdit () {
      this.$emit('save')
    },
    ...mapActions([
      'cancelUEdit'
    ])
  }
}
</script>

<style scoped>
  .fe-ueditor-toolbar-buttons {
    font-size: 12px;
  }
  .fe-toolbar-buttons-border-property-dialog-font-mini {
    font-size: 12px;
  }
  .el-button--mini, .el-button--mini.is-round {
    padding: 5px 8px;
  }
</style>



