<template>
  <div class="fe-col-selector" :style="{height: defaultRowHeight + 'px'}">
    <table class="fe-col-selector-table" cellpadding="0" cellspacing="0" :style="{
      width: defaultColWidth * colNum + 'px',
    }">
      <tr>
        <td v-for="col in colNum" v-bind:key="col" class="fe-col-selector-table-td">{{displayHeadCharactor(col)}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'ColSelector',
  computed: {
    cols () {
      return this.$store.state.cols
    },
    colNum () {
      return this.$store.state.colNum
    },
    defaultRowHeight () {
      return this.$store.state.config.defaultRowHeight
    },
    defaultColWidth () {
      return this.$store.state.config.defaultColWidth
    }
  },
  methods: {
    displayHeadCharactor (i) {
      var charactors = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
      // debugger
      if (i === 26) {
        // debugger
      }
      var time = parseInt((i - 1) / 26)
      var aux = (i - 1) % 26
      var first = (time === 0) ? '' : charactors.substring(time - 1, time)
      var last = charactors.substring(aux, aux + 1)
      return first + last
    }
  }
}
</script>

<style scoped>
  .fe-col-selector {
    display: block;
    margin-left: 50px;
  }
  .fe-col-selector-table {
    border-collapse: collapse;
    border: solid 1px #ddd;
    box-sizing: border-box;
  }
  .fe-col-selector-table-td {
    width: 72px;
    height: 18px;
    background: #e2e9f0;
    text-align: center;
    border: solid 1px #ddd;
    font-size: 12px;
  }
</style>



