<template>
  <div class="fe-row-selector">
    <table class="fe-row-selector-table" cellpadding="0" cellspacing="0" >
      <tr v-for="row in rowNum" v-bind:key="row">
        <td class="fe-row-selector-table-td" :style="{height: defaultRowHeight - 1 + 'px'}">{{row}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
export default {
  name: 'RowSelector',
  computed: {
    rows () {
      return this.$store.state.rows
    },
    rowNum () {
      return this.$store.state.rowNum
    },
    defaultRowHeight () {
      return this.$store.state.config.defaultRowHeight
    }
  }
}
</script>

<style scoped>
  .fe-row-selector {
    float: left;
    width: 50px;
  }
  .fe-row-selector-table {
    border-collapse: collapse;
    border-spacing: 0px;
  }
  .fe-row-selector-table-td {
    width: 50px;
    background: #e2e9f0;
    text-align: center;
    border: solid 1px #ddd;
    font-size: 12px;
  }
</style>



