  SET_CACHE_COPY (state, {row, col, cell}) {
    state.cache.copy.row = row
    state.cache.copy.col = col
    for (let key in cell) {
      state.cache.copy.cell[key] = cell[key]
    }
  },
  CLEAR_CACHE_COPY (state) {
    state.cache.copy.row = undefined
    state.cache.copy.col = undefined
    for (let key in state.cache.copy.cell) {
      Vue.delete(state.cache.copy, key)
    }
  },
  SET_EDITOR (state, editor) {
    // state.editor = editor
    Vue.set(state, 'editor', editor)
  },
  SET_EDITOR_TOOLBAR_STATUS (state, {fontsize, fontfamily}) {
    if (fontsize !== undefined) {
      state.editorToolbarStatus.fontsize = fontsize
    }
    if (fontfamily !== undefined) {
      state.editorToolbarStatus.fontfamily = fontfamily
    }
  },
  REFRESH_TIMESTAMP (state) {
    state.timestamp = new Date().getTime()
  },
  SET_CELLS_BOLD (state) {
    if (state.cache.type !== '') {
      return
    }

    let row1 = state.cache.select.selectedArea.begin.row
    let row2 = state.cache.select.selectedArea.end.row
    let col1 = state.cache.select.selectedArea.begin.col
    let col2 = state.cache.select.selectedArea.end.col

    let allBold = true
    for (let i = row1; i <= row2; i++) {
      for (let j = col1; j <= col2; j++) {
        if (state.cells[i][j] && state.cells[i][j].hidden !== true) {
          if (state.cells[i][j].bold !== true) {
            allBold = false
            break
          }
        }
      }
    }

    if (allBold) {
      for (let i = row1; i <= row2; i++) {
        for (let j = col1; j <= col2; j++) {
          if (state.cells[i][j] && state.cells[i][j].bold) {
            Vue.delete(state.cells[i][j], 'bold')
          }
        }
      }
    } else {
      for (let i = row1; i <= row2; i++) {
        for (let j = col1; j <= col2; j++) {
          if (!state.cells[i][j]) {
            Vue.set(state.cells[i], j, {})
            Vue.set(state.cells[i][j], 'bold', true)
          } else if (state.cells[i][j]) {
            Vue.set(state.cells[i][j], 'bold', true)
          }
        }
      }
    }
  },
