<template>
  <div style="display: flex; flex-direction: column;">
    <!--<editor-toolbar v-if="cacheType === 'edit-cell'"></editor-toolbar>-->
    <toolbar @loading="emitLoading"></toolbar>
    <content-table></content-table>
  </div>
</template>

<script>

import AllSelector from './selector/AllSelector'
import ColSelector from './selector/ColSelector'
import RowSelector from './selector/RowSelector'
import Toolbar from './Toolbar'
import EditorToolbar from './EditorToolbar'
import ContentTable from './ContentTable'

export default {
  name: 'FormEditor',
  components: {
    Toolbar,
    EditorToolbar,
    ContentTable,
    AllSelector,
    ColSelector,
    RowSelector
  },
  computed: {
    cacheType () {
      return this.$store.state.cache.type
    }
  },
  methods: {
    emitLoading () {
      this.$emit('loading')
    }
  }
}
</script>

<style scoped>

</style>



