<!--suppress ALL -->
<template>
  <div class="fe-editor-toolbar-buttons">
    <!--粗体、斜体、下划线-->
    <el-button-group>
      <el-button type="primary" size="mini" title="粗体" @click="execEditorCommand('bold')">
        <i class="iconfont fe-iconfont-bold"></i>
      </el-button>
      <el-button type="primary" size="mini" title="斜体" @click="execEditorCommand('italic')">
        <i class="iconfont fe-iconfont-italic"></i>
      </el-button>
      <el-button type="primary" size="mini" title="下划线" @click="execEditorCommand('underline')">
        <i class="iconfont fe-iconfont-underline"></i>
      </el-button>
    </el-button-group>

    <el-select size="mini" v-model="fontfamily" placeholder="字体" @visible-change="select" @change="execEditorCommand('fontfamily', $event)">
      <el-option
        v-for="item in fontFamilyItems"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>

    <el-select size="mini" v-model="fontsize" placeholder="字号" @visible-change="select" @change="execEditorCommand('fontsize', $event)">
      <el-option
        v-for="item in fontSizeItems"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>

  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  name: 'EditorToolbar',
  data () {
    return {
    }
  },
  computed: {
    fontFamilyItems () {
      return this.$store.state.editorToolbarStatus.fontFamilyItems
    },
    fontSizeItems () {
      return this.$store.state.editorToolbarStatus.fontSizeItems
    },
    fontsize: {
      // getter
      get: function () {
        return this.$store.state.editorToolbarStatus.fontsize
      },
      // setter
      set: function (newValue) {
        this.setEditorToolbarStatusFontsize('newValue')
      }
    },
    fontfamily: {
      // getter
      get: function () {
        return this.$store.state.editorToolbarStatus.fontfamily
      },
      // setter
      set: function (newValue) {
        this.setEditorToolbarStatusFontfamily('newValue')
      }
    }
  },
  methods: {
    execEditorCommand (command, param) {
      if (window.__EDITOR__) {
        window.__EDITOR__.execCommand(command, param)
        // setTimeout(() => {
        //   window.__EDITOR__.focus()
        // }, 1000)
      }
    },
    select () {
      if (!window.__EDITOR__.isFocus()) {
        window.__EDITOR__.focus()
      }
    },
    ...mapActions([
      'setEditorToolbarStatusFontsize',
      'setEditorToolbarStatusFontfamily'
    ])
  }
}
</script>

<style scoped>

  .fe-editor-toolbar-buttons .el-select--mini {
    width: 100px !important;
  }
  .el-button--mini, .el-button--mini.is-round {
    padding: 5px 8px;
  }
  .fe-editor-toolbar-buttons {
    font-size: 12px;
  }
</style>



