<template>
  <div>
    <!--<NEditorToolbar @save="saveUEContent"/>-->
    <script :id=id type="text/plain"></script>
  </div>
</template>
<script>
import UEditorToolbar from './UEditorToolbar'
import NEditorToolbar from './NEditorToolbar'
import { mapActions } from 'vuex'
export default {
  name: 'UEditor',
  components: {
    UEditorToolbar,
    NEditorToolbar
  },
  data () {
    return {
      editor: null
    }
  },
  props: {
    defaultMsg: {
      type: String
    },
    config: {
      type: Object
    },
    id: {
      type: String
    }
  },
  computed: {
    ueditContent () {
      return this.$store.state.cache.uedit.content
    }
  },
  mounted () {
    const _this = this
    // eslint-disable-next-line
    this.editor = UE.getEditor(this.id, this.config) // 初始化UE
    window.__EDITOR__ = this.editor
    this.editor.commands['cancel'] = {
      execCommand: function () {
        _this.cancelUEdit()
      },
      queryCommandState: function () { }
    }

    this.editor.commands['yes'] = {
      execCommand: function () {
        _this.saveUEContent()
      },
      queryCommandState: function () { }
    }

    this.editor.addListener('ready', function () {
      _this.editor.setContent(_this.ueditContent || '') // 确保UE加载完成后，放入内容。
    })
    // console.log('上传这堆错误不用理会，上传接口需自行开发配置')
  },
  methods: {
    getUEContent () { // 获取内容方法
      return this.editor.getContent()
    },
    getUEContentText () { // 获取纯文本内容方法
      return this.editor.getContentTxt()
    },
    saveUEContent () {
      console.log('saveUEContent')
      let source = this.getUEContent()
      this.endUEdit({source})
    },
    ...mapActions([
      'endUEdit',
      'cancelUEdit'
    ])
  },
  destroyed () {
    this.editor.destroy()
  }
}
</script>



